import loginTop from '@/components/login-top'
import inputCell from '@/components/input-cell'
import thirdLogin from '@/components/third-login'
import {
	verifyPhone,
	dealCountDown
} from '@/common/js/common'
import {
	login,
	smsLogin,
	weixinLogin
} from '@/api'
import {
	mapState,
	mapActions
} from 'vuex'
export default {
	name: '',
	data() {
		return {
			phone: '',
			pwd: '',
			methodList: ['密码登录', '验证码登录'],
			current: 0,
			count: 0,
			sms: '',
			tel: '',
			animationData: {}
		}
	},
	onLoad() {
		uni.onWindowResize((res) => {
			console.log(res)
		})
	},
	onShow() {
		this._createAnimation()
	},
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url: '/pages/login/demand-login'
		})
	},
	methods: {
		...mapActions(['reqGetSms']),
		_createAnimation () {










		},
		async onThirdLoginEvent(item) {
			if (item.name == '微信') {
				uni.login({
					provider: 'weixin',
					success: async function(loginRes) {
						console.log(loginRes.authResult);
						let result = await weixinLogin({
							openid: loginRes.authResult.openid
						})
						console.log('result:', result)
						if (result.code == 211) {
							uni.navigateTo({
								url: '/pages/login/bind-phone?openid=' + loginRes.authResult.openid
							})
						}
						if (result.code == 200) {
							uni.reLaunch({
								url: '/pages/home/index/index'
							})
							uni.setStorage({
								key: 'user_id',
								data: result.data.user_id
							})
							uni.setStorage({
								key: 'token',
								data: result.data.token
							})
						}
					},
					fail: function(res) {
						console.log(res)
					}
				});
			}
			if (item.name == 'QQ') {
				uni.showToast({
					title: '暂不支持',
					icon: 'none'
				})
			}
		},
		onMethodTypeClick(index) {
			this.current = index;
		},
		async onLoginEvent() {
			let self = this;
			if (this.current == 0) { // 密码登录
				if (this.phone == '') {
					uni.showToast({
						title: '请输入账号',
						icon: 'none'
					})
					return;
				}
				if (this.pwd == '') {
					uni.showToast({
						title: '请输入密码',
						icon: 'none'
					})
					return;
				}
				let result = await login({
					account: this.phone,
					password: this.pwd
				})
				console.log(result)
				if (result.msg == '登录成功') {
					let pageRoute = uni.getStorageSync('pageRoute');
					// if(pageRoute) {
					// 	uni.navigateBack({
					// 		url: pageRoute
					// 	})
					// 	// uni.navigateBack()
					// }else {
					// 	uni.reLaunch({
					// 		url: '/pages/home/index/index'
					// 	})
					// }
					uni.reLaunch({
						url: '/pages/home/index/index'
					})
					uni.setStorage({
						key: 'user_id',
						data: result.code.userinfo.user_id
					})
					uni.setStorage({
						key: 'token',
						data: result.code.userinfo.token
					})
				}
				return;
			}
			if (this.current == 1) { // 验证码登录
				let flag = verifyPhone(this.tel);
				if (flag) {
					if (this.sms == '') {
						uni.showToast({
							title: '请输入验证码',
							icon: 'none'
						})
						return;
					}
					let result = await smsLogin({
						mobile: this.tel,
						captcha: this.sms
					})
					if (result.msg == '登录成功') {
						let pageRoute = uni.getStorageSync('pageRoute');
						// if(pageRoute) {
						// 	uni.navigateBack({
						// 		url: pageRoute
						// 	})
						// }else {
						// 	uni.reLaunch({
						// 		url: '/pages/home/index/index'
						// 	})
						// }
						uni.reLaunch({
							url: '/pages/home/index/index'
						})
						uni.setStorage({
							key: 'user_id',
							data: result.code.userinfo.user_id
						})
						uni.setStorage({
							key: 'token',
							data: result.code.userinfo.token
						})
					}
				}
			}
		},
		onGetSmsEvent() {
			let self = this;
			let flag = verifyPhone(this.tel);
			if (flag) {
				this.reqGetSms({
					mobile: this.tel,
					callBack: dealCountDown,
					self: self
				})
			}
		},
		onRegisterEvent() {

		}
	},
	components: {
		loginTop,
		inputCell,
		thirdLogin
	}
}
